  margin: 0;
  margin-right: 21px; }
  .socials li:last-child {
    margin-right: 0; }
  @media (max-width: 1023px) {
    .socials li {
      margin-right: 22px; } }

.socials a {
  cursor: pointer;
  transition: all 0.5s ease; }
  .socials a:hover {
    opacity: 0.6; }
  .socials a:active {
    opacity: 0.3; }

.site-sections {
  width: 39%;
  padding: 0;
  padding-top: 5px;
  margin: 0;
  margin-right: 2.5%;
  box-sizing: border-box; }
  @media (max-width: 1023px) {
    .site-sections {
      margin-right: auto; } }
  @media (max-width: 767px) {
    .site-sections {
      position: relative;
      width: 100%;
      padding: 22px 0;
      margin: 0 auto; } }

@media (max-width: 767px) {
  .site-sections::before {
    position: absolute;
    content: "";
    top: 0;
    left: -9%;
    width: 200vw;
    max-width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7); } }

@media (max-width: 767px) {
  .site-sections::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: -9%;
    width: 200vw;
    max-width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7); } }

.site-sections h2 {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  box-sizing: border-box; }
  @media (max-width: 767px) {
    .site-sections h2 {
      margin-bottom: 0; } }

.site-sections ul {
  display: grid;
  grid-template-columns: 55% 45%;
  padding: 0;
  margin: 0;
  list-style: none; }
  @media (max-width: 1023px) {
    .site-sections ul {
      grid-template-columns: 1fr; } }
  @media (max-width: 767px) {
    .site-sections ul {
      padding-top: 20px;
      transition: all 2s ease 0s; } }

@media (max-width: 767px) {
  .footer__group--open ul {
    height: auto;
    overflow: visible;
    opacity: 1; } }

@media (max-width: 767px) {
  .footer__group--close ul {
    height: 0;
    overflow: hidden;
    opacity: 0; } }

.site-sections li {
  width: 80%;
  padding: 0;
  margin: 0;
  margin-bottom: 10px; }
  @media (max-width: 1023px) {
    .site-sections li {
      margin-bottom: 7px; } }

.site-sections a {
  padding: 0;
  margin: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: all 0.5s ease; }
  .site-sections a:last-child {
    margin-bottom: 0; }
  .site-sections a:hover {
    opacity: 0.5; }
  .site-sections a:active {
    opacity: 0.2; }

.contacts {
  padding: 0;
  padding-top: 5px;
  margin: 0;
  box-sizing: border-box; }
  @media (max-width: 1023px) {
    .contacts {
      width: 50%;
      padding-top: 0;
      padding-left: 13px; } }
  @media (max-width: 767px) {
    .contacts {
      position: relative;
      width: 100%;
      padding: 22px 0;
      padding-bottom: 10px;
      margin: 0 auto; } }

@media (max-width: 767px) {
  .contacts::before {
    position: absolute;
    content: "";
    top: 0;
    left: -9%;
    width: 200vw;
    max-width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7); } }

@media (max-width: 767px) {
  .contacts::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: -9%;
    width: 200vw;
    max-width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7); } }

.contacts h3 {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff; }

.contacts__button {
  display: none; }

.contacts__wrapper {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0; }
  @media (max-width: 767px) {
    .contacts__wrapper {
      transition: all 2s ease 0s; } }

@media (max-width: 767px) {
  .footer__group--open .contacts__wrapper {
    height: auto;
    overflow: visible;
    opacity: 1; } }

@media (max-width: 767px) {
  .footer__group--close .contacts__wrapper {
    height: 0;
    overflow: hidden;
    opacity: 0; } }

.contacts p,
.contacts a {
  width: 90%;
  padding: 0;
  margin: 0;
  margin-bottom: 10px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7); }
  @media (max-width: 1023px) {
    .contacts p,
    .contacts a {
      width: 100%; } }

.contacts a {
  margin-bottom: 5px;
  line-height: 25px;
  cursor: pointer;
  transition: all 0.5s ease; }
  .contacts a:hover {
    opacity: 0.7; }
  .contacts a:active {
    opacity: 0.3; }

.accordion {
  display: none; }
  @media (max-width: 767px) {
    .accordion {
      position: absolute;
      content: "";
      top: 28px;
      right: 0;
      display: block;
      width: 15px;
      height: 15px;
      cursor: pointer; }
      .accordion:hover {
        opacity: 0.6; }
      .accordion:active {
        opacity: 0.3; } }

.footer--nojs .accordion {
  display: none; }

.accordion__line {
  width: 100%;
  height: 1px;
  background-color: #c4c4c4; }

.footer__group--close .accordion__line--2 {
  transform: rotate(90deg);
  transition: all 0.5s; }

.footer__group--close .accordion__line--1 {
  transform: translateY(1px);
  transition: all 0.5s; }

